<script>
import { ClipboardUtil } from '@/util/ClipboardUtil'
import DownloadUtil from '@/util/DownloadUtil'

export default {
  name: 'CrudGeneratorView',
  metaInfo: {
    // æ ‡é¢˜ï¼šçªå‡ºå·¥å…·çš„æ ¸å¿ƒåŠŸèƒ½ï¼ˆCRUDä»£ç ç”Ÿæˆï¼‰ã€åº”ç”¨åœºæ™¯ï¼ˆMySQL/PHPï¼‰å’Œä»·å€¼ï¼ˆé«˜æ•ˆï¼‰
    title: 'CRUD å¢åˆ æ”¹æŸ¥ä»£ç ç”Ÿæˆå™¨ - MySQLè¡¨ç»“æ„ä¸€é”®ç”Ÿæˆ PHP Model/Controller | æå‡å¼€å‘æ•ˆç‡',

    meta: [
      // æè¿°ï¼šè¯¦ç»†è¯´æ˜å·¥å…·çš„è¾“å…¥ï¼ˆMySQLç»“æ„ï¼‰å’Œè¾“å‡ºï¼ˆPHPä»£ç ï¼‰ï¼Œä»¥åŠä¸ºå¼€å‘è€…å¸¦æ¥çš„ç›Šå¤„
      {
        name: 'description',
        content: 'å…è´¹åœ¨çº¿ CRUD ä»£ç ç”Ÿæˆå™¨ï¼Œä¸“ä¸º PHP å¼€å‘è€…è®¾è®¡ã€‚æ ¹æ®ä¸Šä¼ çš„ MySQL è¡¨ç»“æ„ï¼Œè‡ªåŠ¨ç”Ÿæˆæ ‡å‡†çš„ Model å’Œ Controller å¢åˆ æ”¹æŸ¥ä»£ç ï¼Œå¿«é€Ÿå®ç°å‰åç«¯åˆ†ç¦»æ¥å£æˆ–åå°ç®¡ç†åŠŸèƒ½ã€‚'
      },

      // å…³é”®è¯ï¼šè¦†ç›–æ ¸å¿ƒåŠŸèƒ½ã€æŠ€æœ¯æ ˆå’Œæ•ˆç‡ç›¸å…³è¯æ±‡
      {
        name: 'keywords',
        content: 'CRUD ç”Ÿæˆå™¨, å¢åˆ æ”¹æŸ¥ä»£ç ç”Ÿæˆ, MySQL ç”Ÿæˆ PHP ä»£ç , Model Controller è‡ªåŠ¨ç”Ÿæˆ, PHP ä»£ç ç”Ÿæˆ, è‡ªåŠ¨åŒ–å¼€å‘å·¥å…·, æå‡æ•ˆç‡'
      }
    ]
  },
  data() {
    return {
      selectedFramework: 'laravel',
      inputMethod: 'manual',
      tableName: '',
      sqlStructure: '',
      uploadedContent: '',
      parsing: false,
      generating: false,
      parsedTables: [],
      selectedTableIndex: 0,
      generateOptions: ['model', 'controller'],
      includeTimestamps: true,
      includeSoftDeletes: false,
      generateComments: true,
      generatedCode: [],
      activeCodeTab: 0,
      
      frameworks: [
        {
          id: 'laravel',
          name: 'Laravel',
          icon: 'ğŸ”¥',
          description: 'Eloquent ORM + èµ„æºæ§åˆ¶å™¨'
        },
        {
          id: 'thinkphp',
          name: 'ThinkPHP',
          icon: 'âš¡',
          description: 'ThinkPHPæ¨¡å‹ + æ§åˆ¶å™¨'
        }
      ]
    }
  },
  computed: {
    currentTable() {
      return this.parsedTables[this.selectedTableIndex] || { name: '', fields: [] }
    }
  },
  methods: {
    selectFramework(frameworkId) {
      this.selectedFramework = frameworkId
      this.generatedCode = []
    },

    onTableChange() {
      this.generatedCode = []
    },

    handleFileUpload(file) {
      const reader = new FileReader()
      reader.onload = (e) => {
        this.uploadedContent = e.target.result
        this.sqlStructure = this.uploadedContent
      }
      reader.readAsText(file)
      return false // é˜»æ­¢è‡ªåŠ¨ä¸Šä¼ 
    },

    async parseTableStructure() {
      this.parsing = true
      try {
        await new Promise(resolve => setTimeout(resolve, 1000)) // æ¨¡æ‹Ÿè§£æ

        const content = this.sqlStructure || this.uploadedContent
        this.parsedTables = this.extractTables(content)
        this.selectedTableIndex = 0

        if (this.parsedTables.length === 1) {
          this.$message.success(`è¡¨ç»“æ„è§£æå®Œæˆï¼Œè§£æåˆ°è¡¨ "${this.parsedTables[0].name}"ï¼ŒåŒ…å« ${this.parsedTables[0].fields.length} ä¸ªå­—æ®µ`)
        } else {
          this.$message.success(`è¡¨ç»“æ„è§£æå®Œæˆï¼Œå…±è§£æåˆ° ${this.parsedTables.length} ä¸ªè¡¨ï¼Œæ€»è®¡ ${this.parsedTables.reduce((sum, table) => sum + table.fields.length, 0)} ä¸ªå­—æ®µ`)
        }
      } catch (error) {
        this.$message.error('è§£æå¤±è´¥ï¼Œè¯·æ£€æŸ¥SQLè¯­æ³•')
      } finally {
        this.parsing = false
      }
    },

    extractTables(sql) {
      const tables = []

      // æ”¹è¿›çš„å¤šè¡¨è§£æé€»è¾‘
      // é¦–å…ˆå°è¯•åŒ¹é…å®Œæ•´çš„CREATE TABLEè¯­å¥ï¼ˆåŒ…æ‹¬è·¨è¡Œçš„æƒ…å†µï¼‰
      const createTableRegex = /CREATE\s+TABLE\s+(?:IF\s+NOT\s+EXISTS\s+)?`?(\w+)`?\s*\(([\s\S]*?)\)\s*(?:ENGINE\s*=\s*\w+)?(?:\s*DEFAULT\s+CHARSET\s*=\s*\w+)?(?:\s*COLLATE\s*=\s*[\w_]+)?(?:\s*COMMENT\s*=\s*'[^']*')?;/gi

      let match
      while ((match = createTableRegex.exec(sql)) !== null) {
        const tableName = match[1]
        const tableContent = match[2]
        const fields = this.extractFieldsFromContent(tableContent)

        if (fields.length > 0) {
          tables.push({ name: tableName, fields })
        }
      }

      // å¦‚æœæ²¡æœ‰æ‰¾åˆ°å®Œæ•´çš„CREATE TABLEè¯­å¥ï¼Œå°è¯•è§£æå•ä¸ªè¡¨ç»“æ„
      if (tables.length === 0) {
        const tableName = this.extractTableName(sql) || 'example_table'
        const fields = this.extractFields(sql)
        if (fields.length > 0) {
          tables.push({ name: tableName, fields })
        }
      }

      // å¦‚æœä»ç„¶æ²¡æœ‰è§£æåˆ°è¡¨ï¼Œè¿”å›ç¤ºä¾‹è¡¨
      return tables.length > 0 ? tables : [{ name: 'example_table', fields: this.getMockFields() }]
    },

    extractTableName(sql) {
      const match = sql.match(/CREATE\s+TABLE\s+(?:IF\s+NOT\s+EXISTS\s+)?`?(\w+)`?/i)
      return match ? match[1] : this.tableName || 'example_table'
    },

    extractFieldsFromContent(content) {
      const fields = []
      const lines = content.split('\n')

      for (const line of lines) {
        const trimmed = line.trim()

        // è·³è¿‡æ³¨é‡Šã€çº¦æŸå’Œç´¢å¼•å®šä¹‰
        if (trimmed.startsWith('--') ||
            trimmed.startsWith('/*') ||
            trimmed.startsWith('PRIMARY KEY') ||
            trimmed.startsWith('KEY') ||
            trimmed.startsWith('INDEX') ||
            trimmed.startsWith('UNIQUE') ||
            trimmed.startsWith('FOREIGN KEY') ||
            trimmed.startsWith('CONSTRAINT') ||
            trimmed === '' ||
            trimmed === ',' ||
            trimmed === ')') {
          continue
        }

        // åŒ¹é…å­—æ®µå®šä¹‰
        const fieldMatch = trimmed.match(/^`?(\w+)`?\s+(\w+(?:\(\d+(?:,\d+)?\))?)\s*(.*?)(?:,\s*)?$/i)
        if (fieldMatch) {
          const [, name, type, rest] = fieldMatch

          // è§£æå­—æ®µå±æ€§
          const nullable = !rest.toUpperCase().includes('NOT NULL')
          const autoIncrement = rest.toUpperCase().includes('AUTO_INCREMENT')
          const defaultValue = this.extractDefault(rest)
          const comment = this.extractComment(rest)

          fields.push({
            name,
            type: type.toLowerCase(),
            nullable,
            autoIncrement,
            default: defaultValue,
            comment: comment || name
          })
        }
      }

      return fields
    },

    extractFields(sql) {
      // ç®€åŒ–çš„å­—æ®µè§£æé€»è¾‘
      const fields = []
      const lines = sql.split('\n')
      
      for (const line of lines) {
        const trimmed = line.trim()
        if (trimmed.startsWith('`') && trimmed.includes('`')) {
          const fieldMatch = trimmed.match(/`(\w+)`\s+(\w+(?:\(\d+\))?)\s*(.*)/i)
          if (fieldMatch) {
            const [, name, type, rest] = fieldMatch
            fields.push({
              name,
              type,
              nullable: !rest.includes('NOT NULL'),
              default: this.extractDefault(rest),
              comment: this.extractComment(rest)
            })
          }
        }
      }
      
      return fields.length > 0 ? fields : this.getMockFields()
    },

    extractDefault(rest) {
      const match = rest.match(/DEFAULT\s+([^,\s]+)/i)
      return match ? match[1].replace(/['"]/g, '') : ''
    },

    extractComment(rest) {
      const match = rest.match(/COMMENT\s+['"](.*?)['"]/i)
      return match ? match[1] : ''
    },

    getMockFields() {
      return [
        { name: 'id', type: 'int(11)', nullable: false, default: '', comment: 'ä¸»é”®ID' },
        { name: 'name', type: 'varchar(255)', nullable: false, default: '', comment: 'åç§°' },
        { name: 'email', type: 'varchar(255)', nullable: false, default: '', comment: 'é‚®ç®±' },
        { name: 'status', type: 'tinyint(1)', nullable: false, default: '1', comment: 'çŠ¶æ€' },
        { name: 'created_at', type: 'timestamp', nullable: true, default: 'NULL', comment: 'åˆ›å»ºæ—¶é—´' },
        { name: 'updated_at', type: 'timestamp', nullable: true, default: 'NULL', comment: 'æ›´æ–°æ—¶é—´' }
      ]
    },

    async generateCode() {
      this.generating = true
      try {
        await new Promise(resolve => setTimeout(resolve, 1500)) // æ¨¡æ‹Ÿç”Ÿæˆ
        
        this.generatedCode = []
        
        if (this.generateOptions.includes('model')) {
          this.generatedCode.push(this.generateModel())
        }

        if (this.generateOptions.includes('controller')) {
          this.generatedCode.push(this.generateController())
        }

        if (this.generateOptions.includes('migration')) {
          this.generatedCode.push(this.generateMigration())
        }

        if (this.generateOptions.includes('request')) {
          this.generatedCode.push(this.generateRequest())
        }
        
        this.activeCodeTab = 0
        this.$message.success('ä»£ç ç”Ÿæˆå®Œæˆ')
      } catch (error) {
        this.$message.error('ç”Ÿæˆå¤±è´¥ï¼Œè¯·é‡è¯•')
      } finally {
        this.generating = false
      }
    },

    generateModel() {
      const className = this.toPascalCase(this.currentTable.name)
      const tableName = this.currentTable.name
      
      if (this.selectedFramework === 'laravel') {
        return {
          filename: `${className}.php`,
          content: `<?php

namespace App\\Models;

use Illuminate\\Database\\Eloquent\\Factories\\HasFactory;
use Illuminate\\Database\\Eloquent\\Model;
${this.includeSoftDeletes ? 'use Illuminate\\Database\\Eloquent\\SoftDeletes;' : ''}

/**
 * ${className} æ¨¡å‹
 * 
 * @property int $id
${this.currentTable.fields.map(field => ` * @property ${this.getPhpType(field.type)} $${field.name} ${field.comment}`).join('\n')}
 */
class ${className} extends Model
{
    use HasFactory${this.includeSoftDeletes ? ', SoftDeletes' : ''};

    /**
     * æ•°æ®è¡¨åç§°
     */
    protected $table = '${tableName}';

    /**
     * å¯æ‰¹é‡èµ‹å€¼çš„å±æ€§
     */
    protected $fillable = [
${this.currentTable.fields.filter(f => !['id', 'created_at', 'updated_at'].includes(f.name)).map(f => `        '${f.name}',`).join('\n')}
    ];

    /**
     * å±æ€§ç±»å‹è½¬æ¢
     */
    protected $casts = [
${this.currentTable.fields.map(f => this.getCastType(f)).filter(Boolean).map(cast => `        ${cast},`).join('\n')}
    ];

