    // è¾…åŠ©æ–¹æ³•
    toPascalCase(str) {
      return str.replace(/(^|_)([a-z])/g, (match, p1, p2) => p2.toUpperCase())
    },

    toCamelCase(str) {
      const pascal = this.toPascalCase(str)
      return pascal.charAt(0).toLowerCase() + pascal.slice(1)
    },

    getPhpType(sqlType) {
      if (sqlType.includes('int')) return 'int'
      if (sqlType.includes('varchar') || sqlType.includes('text')) return 'string'
      if (sqlType.includes('decimal') || sqlType.includes('float')) return 'float'
      if (sqlType.includes('timestamp') || sqlType.includes('datetime')) return 'string'
      return 'mixed'
    },

    getCastType(field) {
      if (field.type.includes('int')) return `'${field.name}' => 'integer'`
      if (field.type.includes('decimal') || field.type.includes('float')) return `'${field.name}' => 'float'`
      if (field.type.includes('json')) return `'${field.name}' => 'array'`
      if (field.type.includes('timestamp') || field.type.includes('datetime')) return `'${field.name}' => 'datetime'`
      return null
    },

    getThinkPhpType(field) {
      if (field.type.includes('int')) return `'${field.name}' => 'integer'`
      if (field.type.includes('decimal') || field.type.includes('float')) return `'${field.name}' => 'float'`
      if (field.type.includes('json')) return `'${field.name}' => 'json'`
      if (field.type.includes('timestamp') || field.type.includes('datetime')) return `'${field.name}' => 'timestamp'`
      return null
    },

    getValidationRule(field) {
      let rules = []
      
      if (!field.nullable) rules.push('required')
      
      if (field.type.includes('varchar')) {
        const match = field.type.match(/varchar\((\d+)\)/)
        if (match) rules.push(`max:${match[1]}`)
      }
      
      if (field.name.includes('email')) rules.push('email')
      if (field.type.includes('int')) rules.push('integer')
      
      return rules.join('|') || 'nullable'
    },

    getMigrationField(field) {
      let line = '            $table->'
      
      if (field.name === 'id' && field.type.includes('int')) {
        line += 'id()'
      } else if (field.type.includes('varchar')) {
        const match = field.type.match(/varchar\((\d+)\)/)
        const length = match ? match[1] : '255'
        line += `string('${field.name}', ${length})`
      } else if (field.type.includes('text')) {
        line += `text('${field.name}')`
      } else if (field.type.includes('int')) {
        line += `integer('${field.name}')`
      } else if (field.type.includes('timestamp')) {
        if (field.name === 'created_at' || field.name === 'updated_at') {
